<template>
    <div>
        <h1>Lojinha do Samir</h1>
        <progress-bar v-if="loadingProducts"></progress-bar>

        <div v-else class="store-products">
            <product-card v-for="(product, index) in productNotSoldFirst" 
                :key="index"
                :product="product"
                @click="showProductDetails"
            ></product-card>    
        </div>  

        <product-details-modal
            :show="showProductDetailModal"
            :product="selectedProduct"
            @backgroundClick="closeProductDetailModal()"
            @closeButtonClick="closeProductDetailModal()"
        ></product-details-modal>      
    </div>
</template>

<script>
import ProductCard from "@/components/store/ProductCard";
import ProductDetailsModal from "@/components/store/ProductDetailsModal";
import ProgressBar from "@/components/ProgressBar";
import StoreService from "@/service/StoreService";

export default {
    components:{
        "product-card": ProductCard,
        "product-details-modal": ProductDetailsModal,
        "progress-bar": ProgressBar
    },

    data(){
        return {
            loadingProducts: true,
            showProductDetailModal: false,
            selectedProduct: null,
            products: []
        }
    },

    computed:{
        "productNotSoldFirst"(){
            let products = [];

            for(let product of this.products){
                if(!product.sold){
                    products.splice(0, 0, product);
                }else{
                    products.push(product);
                }
            }

            return products;
        }
    },

    created(){
        StoreService.getProductsByFunction((products) => {
            this.products = products;
            this.loadingProducts = false;
        });
    },

    methods:{
        showProductDetails(product){
            this.selectedProduct = product;
            this.showProductDetailModal = true;
        },

        closeProductDetailModal(){
            this.showProductDetailModal = false;
        }
    }
}
</script>

<style scoped>
h1 {
    padding: 20px;
    margin: 0;
    font-family: "Segoe UI",Arial,sans-serif;
}

.store-products{
    text-align: center;
    margin: 0 20px;
}
</style>