<template>
    <generic-modal 
        :show="show"
        @backgroundClick="$emit('backgroundClick')"
        @closeButtonClick="$emit('closeButtonClick')">
        
        <div style="position: relative" v-if="product !== null">
            <h1>{{ product.name }}</h1>

            <image-slide-show 
                :srcArray="imageSrcArray"
            ></image-slide-show>

            <p>{{ product.about }}</p>

            <sold-label v-if="product.sold"></sold-label>
            <price-label v-else :price="product.price"></price-label>
        </div>
    </generic-modal>
</template>

<script>
import GenericModal from "@/components/modal/GenericModal";
import ImageSlideShow from "@/components/ImageSlideShow";
import SoldLabel from "@/components/store/SoldLabel";
import PriceLabel from "@/components/store/PriceLabel";

export default {
    components:{
        "generic-modal": GenericModal,
        "image-slide-show": ImageSlideShow,
        "sold-label": SoldLabel,
        "price-label": PriceLabel
    },

    props:{
        show:{
            type: Boolean,
            default: false
        },

        product:{
            type: Object,
            default: null
        }
    },

    computed:{
        "imageSrcArray"(){
            let imageSrcArray = [];

            if(this.product !== null){
                for(let image of this.product.images){
                    imageSrcArray.push(image.src);
                }
            }
            return imageSrcArray;
        }
    }
}
</script>