// const JSON_URL = "https://raw.githubusercontent.com/SamirSales/pickle-wiki-frontend-reactj/master/package.json";
const JSON_URL = "https://raw.githubusercontent.com/SamirSales/samirsales.github.io/master/data/store/products.json";

export default {
    getProductsByFunction(productsRetrieverFunction){
        fetch(JSON_URL).then(response => {
            response.text().then(text =>{
                // console.log("text", text)
                productsRetrieverFunction(JSON.parse(text));
            });
        });
    }
}
