<template>
    <div class="image-slide-show">
        <img :src="getSelectedImage()">
        <p>{{ getIndexLabelText() }}</p>

        <button 
            class="previous-btn" 
            @click="goToPreviousIndex()"
        >&#10094;</button>
        
        <button 
            class="next-btn" 
            @click="goToNextIndex()"
        >&#10095;</button>
    </div>
</template>

<script>
export default {
    props:{
        srcArray:{
            type: Array,
            default: () => []
        }
    },

    data(){
        return{
            index: 0
        }
    },

    watch:{
        "srcArray"(){
            this.index = 0;
        }
    },

    methods:{
        getSelectedImage(){
            return this.srcArray[this.index];
        },

        getIndexLabelText(){
            return (this.index + 1) + " / " + this.srcArray.length;
        },

        goToNextIndex(){
            this.index++;

            if(this.index >= this.srcArray.length){
                this.index = 0;
            }
        },

        goToPreviousIndex(){
            this.index--;

            if(this.index <= 0){                
                this.index = this.srcArray.length - 1;
            }
        }
    }
}
</script>

<style scoped>
.image-slide-show{
    position: relative;
}

.image-slide-show img{
    width: 100%;
    height: 350px;
    object-fit: scale-down;
    background-color: #ffffff;
}

.image-slide-show button{
    position: absolute;
    font-size: 50px;
    top: 0;
    height: 350px;
    padding: 0 20px;
    background-color: rgba(255, 255, 255, 0); 
    color: #c0c0c0;
    border: solid 0px #e0e0e0;
}

.image-slide-show button:focus{
    outline: none;
}

.image-slide-show button:hover{
    color: black;
}

.image-slide-show .previous-btn{
    left: 0px;
}

.image-slide-show .next-btn{
    right: 0px;
}
</style>